body {
  background: #eee;
  padding-top: 20px;
  padding-bottom: 20px;
}
h1,h2,h3,h4,h5,h6,p {
  margin: 0;
}
.App {
  min-height: 100vh;
  color: #333;
  position: relative;
  width: 1024px;
  padding: 20px 40px;
  background: #fff;
  margin: 0 auto;
  box-shadow: 0 0 30px #bbb;
  border-radius: 20px;
}
.header h3 {
  line-height: 1;
}
.logo {
  width: 200px;
  text-align: center;
}
.sponsor {
  width: 200px;
  right: 30px;
  top: 30px;
  position: absolute;
}
.sponsor img {
  width: 100%;
  height: auto;
}
.main {
  padding: 40px 0;
}
.main_title {
  font-size: 32px;
  text-align: center;
  font-weight: bold;
  margin-top: 40px;
}
.dropzone {
  cursor: pointer;
  border: 2px dashed #ccc;
  border-radius: 10px;
  text-align: center;
  height: 200px;
  padding: 40px;
  font-size: 20px;
}
.dropzone:hover {
  background: #ccc;
}
button {
  margin-top: 30px;
  cursor: pointer;
  margin-bottom: 30px;
  width: 200px;
  text-transform: uppercase;
  padding: 16px;
  font-size: 18px;
  background: #585;
  color: #fff;
  border: none;
  border-radius: 30px;
}
.upload button:hover {
  background: #474;
}
.column {
  padding: 20px;
  cursor: pointer;
  text-align: center;
  width: 280px;
  border-radius: 10px;
}
.result_preview {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  margin-top: 40px;
}
.result h1 {
  margin-bottom: 20px;
}
.success {
  border: 1px solid #383;
  color: #383;
}
.success:hover {
  background:#383;
  color: #fff; 

}
.success_checked {
  background:#383;
  color: #fff; 
}
.warning {
  border: 1px solid #b50;
  color: #b50;
}
.warning:hover {
  background:#b50;
  color: #fff; 

}
.warning_checked {
  background:#b50;
  color: #fff; 
}
.error {
  border: 1px solid #a33;
  color: #a33;

}
.error:hover {
  background:#a33;
  color: #fff; 

}
.error_checked {
  background:#a33;
  color: #fff; 
}
.toolbar {
  position: absolute;
  right: 0;
  top: 0;
}
.upload {
  position: relative;
}
.result_images > div {
  height: 200px;
  border: 1px solid #ccc;
  width: 30%;
  margin-bottom: 60px;
  position: relative;
  border: 4px solid #fff;
  cursor: pointer;
}
.result_images > div:hover {
  border: 4px solid #00f;
}

.result_images {
  display:flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.result_images img {
  display:flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.chance {
  position: absolute;
  width: 200px;
  bottom: -42px;
  right: calc(50% - 120px);
  padding: 10px 20px;
}